:root {
  --color-primary: #6741d9;
  --color-primary-light: #7950f2;
  --color-text: #dee2e6;
  --color-text-dark: #adb5bd;
  --color-background-100: #343a40;
  --color-background-500: #2b3035;
  --color-background-900: #212529;
  --color-red: #fa5252;
  --color-red-dark: #e03131;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

  color: var(--color-text);
  background-color: #081e24;
  padding: 2.4rem;

}


/* ******* */

.nav-bar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  height: 7.2rem;
  padding: 0 3.2rem;
  background-color: #368091;
  border-radius: 0.9rem;
}

.main {
  margin-top: 2.4rem;
  height: calc(100vh - 7.2rem - 3 * 2.4rem);
  display: flex;
  gap: 2.4rem;
  justify-content: center;
}

.box {
  width: 50%;
  max-width: 50%;
  background-image: linear-gradient(rgb(18, 35, 37), rgb(44, 78, 87));
  border-radius: 0.9rem;
  overflow: auto;
  position: relative;
  height: 100%;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #2c3f42;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #057179;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #2dc7e9;
}

.loader {
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 600;
  margin: 4.8rem;
}

.error {
  text-align: center;
  font-size: 2rem;
  padding: 4.8rem;
}

/* ******* */

.logo {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.logo span {
  font-size: 3.2rem;
}

.logo h1 {
  font-size: 2.4rem;
  font-weight: 800;
  color: #0d3b46;
  transition: 0.5s;
}

.logo h1:hover {
  text-decoration: underline;
}

.search {

  justify-self: center;
  border: none;
  padding: 1.1rem 1.6rem;
  font-size: 1.8rem;
  border-radius: 0.7rem;
  width: 60rem;
  transition: all 0.3s;
  color: #bbf3ff;

  /* background-color: var(--color-background-900); */
  background-image: linear-gradient(rgb(55, 94, 105), rgb(17, 31, 35));
}

.search::placeholder {
  color: #bbf3ff;
}

.search:focus {
  outline: none;
  box-shadow: 0 2.4rem 2.4rem rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}

.num-results {
  justify-self: end;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  color: #0d3b46;

}

.btn-toggle {
  position: absolute;
  top: 0.8rem;
  right: 0.8rem;
  height: 2.4rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  background-color: rgb(0, 118, 139);
  color: var(--color-text);
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  z-index: 999;
  box-shadow: 0 0 1px black;
}

.list {
  list-style: none;
  padding: 0.8rem 0;
  overflow: auto;
}

.list-watched {
  height: calc(100% - 9rem);
}

.list li {
  position: relative;
  display: grid;
  grid-template-columns: 4rem 1fr;
  grid-template-rows: auto auto;
  column-gap: 2.4rem;
  font-size: 1.6rem;
  align-items: center;

  padding: 1.6rem 3.2rem;
  border-bottom: 1px solid #1c515d;
}

.list.list-movies li {
  cursor: pointer;
  transition: all 0.3s;
}

.list.list-movies li:hover {
  background-color: #296673;
}

.list img {
  width: 100%;
  grid-row: 1 / -1;
}

.list h3 {
  font-size: 1.8rem;
}

.list div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
}

.list p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.btn-delete {
  position: absolute;
  right: 2.4rem;

  height: 2.8rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  background-color: #21606e;
  color: white;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-delete:hover {
  background-color: var(--color-red-dark);
}

/* ******* */

.summary {
  padding: 2.2rem 3.2rem 1.8rem 3.2rem;
  border-radius: 0.9rem;
  background-color: #368091;
  box-shadow: 0 1.2rem 2.4rem rgba(0, 0, 0, 0.2);
  color: #0d3b46;
  font-size: 15px;
  font-weight: 700;
}

.summary h2 {
  text-transform: uppercase;
  font-size: 1.8rem;
  margin-bottom: 0.6rem;
}

.summary div {
  display: flex;
  align-items: center;
  gap: 2.4rem;
  font-size: 1.6rem;
  font-weight: 700;
}

.summary p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

/* ******* */

.details {
  line-height: 1.4;
  font-size: 1.4rem;
}

.details header {
  display: flex;
}

.details section {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

.details img {
  width: 33%;
}

.details-overview {
  width: 100%;
  padding: 2.4rem 3rem;
  background-color: #104754;
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
}

.details-overview h2 {
  font-size: 2.4rem;
  margin-bottom: 0.4rem;
  line-height: 1.1;
}

.details-overview p {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

.rating {
  background-color: #01adc0;
  border-radius: 0.9rem;
  padding: 2rem 2.4rem;
  margin-bottom: 0.8rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  text-align: center;
}

.btn-add {
  background-color: #11373f;
  color: var(--color-text);
  border: none;
  border-radius: 10rem;
  font-size: 1.4rem;
  padding: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-add:hover {
  background-color: #1c6a7c;
}

.seconds {
  background-color: var(--color-background-100);
  width: 8rem;
  border-radius: 10rem;
  font-weight: 600;
  text-align: center;
  padding: 0.4rem;
  margin-top: 2.4rem;
}

.btn-back {
  position: absolute;
  top: 0.6rem;
  left: 0.6rem;
  height: 3.2rem;
  aspect-ratio: 1;
  border-radius: 50%;
  border: none;
  /* background-color: var(--color-text); */
  background-color: #fff;
  color: var(--color-background-500);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8);
  font-family: sans-serif;
  font-size: 2.4rem;
  font-weight: 1000;
  cursor: pointer;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.plot {
  background-color: #018b9a;
  padding: 10px;
  width: 100%;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  text-align: center;
}

.actor {
  background-color: #015d67;
  padding: 10px;
  width: 100%;
  border: none;
  border-radius: 6px;
  font-weight: 700;
  text-align: center;

}

.director {
  background-color: #014148;
  padding: 10px;
  width: 100%;
  border: none;
  border-radius: 6px;
  font-weight: 700;
  text-align: center;


}


/* Default styles for larger screens */
/* ... */

/* Styles for tablets and smaller screens */
@media (max-width: 768px) {
  .nav-bar {
    grid-template-columns: 1fr 1fr;
    padding: 0 1.6rem;
  }

  .search {
    width: 100%;
    max-width: 30rem;
  }

  .main {
    flex-direction: column;
    align-items: center;
  }

  .box {
    width: 100%;
    max-width: none;
    height: 80vh;
  }


  /* Other adjustments for tablet screens */
}

/* Styles for smartphones */
@media (max-width: 480px) {
  body {
    padding: 1.2rem;
  }

  .nav-bar {
    grid-template-columns: 1fr;
    height: auto;
    padding: 1.2rem;
    text-align: center;
  }

  .logo,
  .search,
  .num-results {
    justify-self: start;
    margin-bottom: 1.2rem;
  }

  .main {
    margin-top: 1.2rem;
    height: 100vh;
  }

  .details header {
    flex-direction: column;
  }

  .details img {
    width: 100%;
  }

  .box {
    width: 100%;
    max-width: none;
    height: 80vh;
  }


  .star-rating {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: auto;
    height: 65%;
  }

  .star {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: auto;
    height: auto;
    font-size: 1.0rem;
    /* Adjust font size */
    width: 1.2rem;
    /* Adjust star size */
    height: 1.2rem;
  }





  /* Other adjustments for smartphone screens */
}

/* Footer */
footer {
  margin-top: 20px;
  background-image: linear-gradient(rgb(32, 55, 61), rgb(15, 33, 39));
  color: #009faa;
  padding: 40px 20px;
  font-size: 15px;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.footer-links ul,
.footer-social ul {
  list-style: none;
  padding: 0;
}

.footer-links a,
.footer-social a {
  color: #76868a;
  text-decoration: none;
}

.footer-newsletter input {
  padding: 10px;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
}

.footer-newsletter button {
  padding: 10px 20px;
  background-color: #24a2ad;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.footer-bottom {
  text-align: center;
  font-size: 18px;
}

@media (max-width: 768px) {
  .footer-container {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
}